.btn {
    display: inline-block;
    background: #F87933;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    color: #fff;
    padding: 24px 36px;
    border-radius: 38px;
    border: 1px solid #F87933;
    &:hover{
        background-color: #fff;
        color: #F87933;
    }
}